.Profile {
  min-height: 275vh;
}
.Profile svg:hover {
  color: var(--primaryColor);
}
.CharacterContainer [class*="col-"] {
  margin: 0;
  padding: 0 2px;
}

.AddCharacter {
  min-height: 49px;
}
.MainAlt {
  border: 1px solid var(--light_grey);
  border-radius: 4px;
  background-color: var(--slate_grey);
}
.MainAlt .checkbox {
  margin-left: 4px;
  padding: 0;
  display: inline-flex;
}
.MainAlt .checkBoxText {
  font-weight: bold;
  font-size: 18px;
  color: var(--grey);
}
