/* 	The Bootstrap grid system has four classes:
 xs (for phones - screens less than 768px wide)
 sm (for tablets - screens equal to or greater than 768px wide)
 md (for small laptops - screens equal to or greater than 992px wide)
 lg (for laptops and desktops - screens equal to or greater than 1200px wide)
 max-width: 767px = If [device width] is less than or equal to 767px, then do {}
 min-width: 767px = If [device width] is greater than or equal to 767px, then do {}
 */
:root {
  --calendarMobileTileHeight: 7vh;
}

@media (max-width: 767px) {
  .EventList {
    min-height: calc(var(--calendarMobileTileHeight) * 6 + 86px);
  }
  .hasEventsContainerMobile {
    position: relative;
    height: 2vh;
  }
  .eventLabelColor {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    flex-shrink: 0;
    display: inline-flex;
    margin: 4px auto 0 auto;
  }
  .react-calendar__month-view button {
    height: var(--calendarMobileTileHeight);
  }
  .react-calendar__tile abbr {
    position: relative;
    top: 0;
    left: 0;
    float: none;
    margin: 0 auto;
    display: block;
  }
}
